.sdk-button {
    width: 300px;
    height: 250px;
    border: 2px solid lightgray;
    border-radius: 10px;
    background-color: white;
    margin: 0;

}

.sdk-button-content {
    padding: 20px;
}

.sdk-name {
    vertical-align: top;
    margin: 0;
}

.sdk-desc {
    padding-top: 50px;
    margin: 0;
    line-height: 24px;
}

.sdk-button-hr {
    width: 30%;
    height:5px;
    border: 0px;
}
